export default {
  install: (Vue) => {
    Vue.prototype.$successToast = (message, duration = 1500) => {
      Vue.prototype.$message({
        type: 'success',
        message,
        duration
      })
    }
    Vue.prototype.$errorToast = (message, duration = 1500) => {
      Vue.prototype.$message({
        type: 'error',
        message,
        duration
      })
    }
    Vue.prototype.$errorNotify = (message = '', duration = 0, position = 'bottom-right') => {
      Vue.prototype.$notify({
        type: 'error',
        title: '',
        message,
        duration,
        position
      })
    }
  }
}



