<template>
    <div>
        <div class="box">
            <el-form label-position="right" label-width="80px">
                <el-form-item label="">
                  <el-radio v-model="formData.role" label="admin"></el-radio>
                  <template v-if="isRegister()">
                    <el-radio v-model="formData.role" label="teacher"></el-radio>
                    <el-radio v-model="formData.role" label="student"></el-radio>
                  </template>
                  <template v-else>
                    <el-radio v-model="formData.role" label="student"></el-radio>
                  </template>
                </el-form-item>
                <el-form-item label="">
                    <el-input placeholder="" v-model="formData.account"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input
                        placeholder=""
                        type="password"
                        v-model="formData.password"></el-input>
                </el-form-item>
                <el-form-item label="" v-show="isRegister()">
                    <el-input
                        placeholder=""
                        type="password"
                        v-model="formData.confirmPassword">
                    </el-input>
                </el-form-item>
                <el-button @click="submit" :type="buttonType">{{type}}</el-button>
                <a @click="swap" href="javascript:void(0)" class="toggle">{{toggle}}</a>
            </el-form>
        </div>
    </div>
</template>

<script>
import { BUTTON_TYPE } from 'common/base'
import {
  adminLogin,
  adminRegister,
  userLogin,
  userRegister } from 'api/account'
const HOME = 0
const BACK = 1
export default {
  name: 'Login',
  data () {
    return {
      type: '',
      toggle: '',
      errorMsg: '',
      buttonType: BUTTON_TYPE,
      formData: {
        account: '',
        password: '',
        confirmPassword: '',
        role: 'student'
      },
      status: {
        result: true,
        message: ''
      }
    }
  },
  methods: {
    swap () {
      [this.type, this.toggle] = [this.toggle, this.type]
    },
    isRegister () {
      return this.type === ''
    },
    checkRegister () {
      const status = this.status
      const formData = this.formData
      this.checkLogin()
      if (!status.result) {
        return false
      }
      if (formData.password !== formData.confirmPassword) {
        status.result = false
        status.message = ''
      }
      this.showError()
      return status.result
    },
    checkLogin () {
      const status = this.status
      const formData = this.formData
      status.result = true
      if (formData.account === '') {
        status.result = false
        status.message = ''
      } else if (formData.password === '') {
        status.result = false
        status.message = ''
      } else if (formData.password.length < 6 || formData.password.length > 20) {
        status.result = false
        status.message = '6-20'
      }
      this.showError()
      return status.result
    },
    showError () {
      const status = this.status
      if (!status.result) {
        this.$errorToast(status.message)
      }
    },
    submit () {
      const formData = this.formData
      let promise
      if (this.type === '' && this.checkLogin()) {
        if (formData.role === 'admin') {
          promise = adminLogin(formData.account, formData.password)
        } else {
          promise = userLogin(formData.account, formData.password)
        }
        promise
          .then(res => {
            sessionStorage.username = this.formData.account
            if (formData.role === 'admin') {
              sessionStorage.role = 'admin'
            } else {
              sessionStorage.role = 'user'
            }
            if (sessionStorage.getItem('goback')) {
              sessionStorage.removeItem('goback')
              this.successGo(BACK, '')
            } else {
              this.successGo(HOME, '')
            }
          })
          .catch(error => {
            this.$errorNotify(error)
          })
      } else if (this.checkRegister()) {
        let promise
        if (formData.role === 'admin') {
          promise = adminRegister(formData.account, formData.password)
        } else {
          const role = formData.role === 'teacher' ? 'teacher' : 'student'
          promise = userRegister(formData.account, formData.password, role)
        }
        promise
          .then(
            res => {
              if (res.code === 2000) {
                this.successGo(HOME, '')
              } else {
                this.$errorNotify(res.message)
              }
            }
          )
          .catch(error => {
            this.$errorNotify(error)
          })
      }
    },
    successGo (target, message) {
      this.$successToast(message, 1500)
      if (target === HOME) {
        setTimeout(this._goHome, 1500)
      } else {
        setTimeout(this._goBack, 1500)
      }
    },
    _goHome () {
      if (this.formData.role === 'admin') {
        this.$router.push({path: '/admin/student/list'})
      } else {
        this.$router.push({path: '/user/resource/browse'})
      }
    },
    _goBack () {
      this.$router.go(-1)
    }
  },
  activated () {
    if (this.$route.path.split('/')[2] === 'signup') {
      this.type = ''
      this.toggle = ''
    } else {
      this.type = ''
      this.toggle = ''
    }
  }
}
</script>

<style lang="stylus" scoped>
.box
  position fixed
  top 50%
  left 50%
  transform translate(-50%,-100%)
  background #fff
  padding 30px
  border-radius 5px
  width 400px
  text-align center
  .toggle
    margin-left 10px
    color #ffa100
</style>



