<template>
  <div>
    <category-input
      :initForm="initForm"
      :formData="formData"
      @confirm="confirm"></category-input>
  </div>
</template>
<script>
import CategoryInput from 'components/basic/input'
import { addCategory } from 'api/category'
export default {
  data () {
    return {
      initForm: [
        {
          label: '',
          prop: 'categoryName',
          required: true
        },
        {
          label: '',
          prop: 'categoryDescription',
          required: true
        }
      ],
      formData: {
        categoryName: '',
        categoryDescription: ''
      }
    }
  },
  methods: {
    confirm () {
      addCategory(this.formData)
        .then(() => {
          this.$successToast('')
          this.formData = {
            categoryName: '',
            categoryDescription: ''
          }
        })
        .catch(err => {
          this.$errorNotify(err)
        })
    }
  },
  components: {
    CategoryInput
  }
}
</script>



