<template>
  <div class="container">
    <template v-if="tableData">
      <basic-table :tableInit="tableInit" :tableData="tableData">
        <template slot-scope="scope">
          <el-button
            :type="buttonType"
            round
            size="small"
            @click="handleEdit(scope.$index)"> <i class="el-icon-edit el-icon--right"></i>
          </el-button>
        </template>
      </basic-table>
    </template>
    <el-pagination
      v-show="total > 0"
      layout="prev, pager, next"
      @current-change="handlePageChange"
      :page-size="pageSize"
      :total="total"></el-pagination>
    <el-dialog
      :visible.sync="editDialogShow"
      width="20%">
      <edit
        :initForm="initEditForm"
        :currentData="currentData"
        @hiddenDialog="hiddenDialog"
        @confirmForm="confirmEditForm"></edit>
    </el-dialog>
  </div>
</template>
<script>
import Edit from 'components/basic/edit'
import BasicTable from 'components/basic/table'
import { getStudentList, editStudent } from 'api/student'
import { STUDENT_LIMIT } from 'common/student'
import { BUTTON_TYPE } from 'common/base'
export default {
  name: 'StudentList',
  data () {
    return {
      tableData: null,
      total: 0,
      pageSize: STUDENT_LIMIT,
      buttonType: BUTTON_TYPE,
      editDialogShow: false,
      currentData: null,
      editIndex: -1
    }
  },
  methods: {
    handlePageChange (page) {
      this._getStudentList(page)
    },
    handleEdit (index) {
      this.editIndex = index
      this.currentData = Object.assign({}, this.tableData[index])
      this.editDialogShow = true
    },
    hiddenDialog () {
      this.editDialogShow = false
    },
    confirmEditForm () {
      editStudent(this.currentData)
        .then(() => {
          this.$successToast('')
          this.$set(this.tableData, this.editIndex, this.currentData)
          this.editDialogShow = false
        })
        .catch(err => {
          this.$errorNotify(err)
        })
    },
    _getStudentList (page) {
      getStudentList(page)
        .then(res => {
          this.tableData = res.dataList
          this.total = res.databaseSum
        })
    }
  },
  mounted () {
    this._getStudentList(1)
  },
  created () {
    this.tableInit = [
      {
        label: '',
        prop: 'studentName'
      },
      {
        label: '',
        prop: 'studentClass'
      },
      {
        label: '',
        prop: 'studentTeacher'
      },
      {
        label: '',
        prop: 'studentIntroduction'
      }
    ]
    this.initEditForm = [
      {
        label: '',
        prop: 'studentClass'
      },
      {
        label: '',
        prop: 'studentTeacher'
      },
      {
        label: '',
        prop: 'studentIntroduction'
      }
    ]
  },
  components: {
    Edit,
    BasicTable
  }
}
</script>
<style lang="stylus" scoped>
.container
  width 80%
  margin auto
  text-align center
  .el-pagination
    margin-top 10px
</style>



