<template>
  <div>
    <student-input
      :initForm="initForm"
      :formData="formData"
      @confirm="confirm"></student-input>
  </div>
</template>
<script>
import StudentInput from 'components/basic/input'
import { inputStudent } from 'api/student'
export default {
  name: 'AddStudent',
  data () {
    return {
      initForm: [
        {
          label: '',
          prop: 'studentName',
          required: true
        },
        {
          label: '',
          prop: 'studentClass',
          required: true
        },
        {
          label: '',
          prop: 'studentTeacher',
          required: false
        },
        {
          label: '',
          prop: 'studentIntroduction',
          required: true
        }
      ],
      formData: {
        studentName: '',
        studentClass: '',
        studentTeacher: '',
        studentIntroduction: ''
      }
    }
  },
  methods: {
    confirm () {
      inputStudent(this.formData)
        .then(() => {
          this.$successToast('')
          this.formData = {
            studentName: '',
            studentClass: '',
            studentTeacher: '',
            studentIntroduction: ''
          }
        })
        .catch(err => {
          this.$errorNotify(err)
        })
    }
  },
  components: {
    StudentInput
  }
}
</script>



