<template>
  <div>
    <r-header
      @logout="handleLogout"
      :default-active="defaultActive"
      :header="headerList"></r-header>
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
  </div>
</template>
<script>
import RHeader from 'components/basic/r-header'
import { adminLogout } from 'api/account'
export default {
  name: 'AdminIndex',
  created () {
    this.defaultActive = '/admin/student/list'
    this.headerList = [
      {
        title: '',
        submenu: [
          {
            title: '',
            route: '/admin/student/list'
          },
          {
            title: '',
            route: '/admin/student/input'
          }
        ]
      },
      {
        title: '',
        route: '/admin/student/search'
      },
      {
        title: '',
        submenu: [
          {
            title: '',
            route: '/admin/resource/browse'
          },
          {
            title: '',
            route: '/admin/category/info'
          },
          {
            title: '',
            route: '/admin/category/add'
          }
        ]
      },
      {
        title: '',
        route: '/admin/communicate/browse'
      }
    ]
  },
  methods: {
    handleLogout () {
      adminLogout()
        .then(() => {
          this.$successToast('')
          sessionStorage.removeItem('role')
          sessionStorage.removeItem('username')
          setTimeout(() => {
            this.$router.push({path: '/account/signin'})
          }, 1500)
        })
        .catch(err => {
          this.$errorNotify(err)
        })
    }
  },
  components: {
    RHeader
  }
}
</script>



