import axios from 'axios'
// qs  post  data 
// axios postContent-type application/json
import qs from 'qs'
import Router from '@/router/index'
import { ERR_OK, UNAUTHORIZED } from 'api/config'
axios.defaults.timeout = 5000
axios.defaults.baseURL = 'http://www.yoryz.cn/Share'
axios.defaults.withCredentials = true

/**
 *  get  post 
 *  @param url
 *  @param params
 *  @returns {Promise}
 */

// http request 
axios.interceptors.request.use(
  request => {
    if (request.data instanceof FormData) {
      request.headers = {
        'Content-Type': 'multipart/form-data '
      }
    } else {
      request.data = qs.stringify(request.data)
      request.headers = {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    }
    return request
  },
  error => {
    return Promise.reject(error)
  }
)
// http response 
axios.interceptors.response.use(response => {
  if (response.status === 200 && response.data.code === UNAUTHORIZED) {
    sessionStorage.setItem('goback', 1)
    Router.push({path: '/account/signin'})
    return response
  } else {
    return response
  }
})
export function $get (url, params = {}) {
  return new Promise((resolve, reject) => {
    axios.get(url, {params})
      .then(response => {
        response = response.data
        if (response.code === ERR_OK) {
          resolve(response)
        } else {
          reject(response.message)
        }
      })
      .catch(error => reject(error))
  })
}
export function $post (url, params = {}) {
  return new Promise((resolve, reject) => {
    axios.post(url, params)
      .then(response => {
        response = response.data
        if (response.code === ERR_OK) {
          resolve(response)
        } else {
          reject(response.message)
        }
      })
      .catch(error => reject(error))
  })
}



