<template>
  <div class="box">
    <el-form label-position="right" label-width="90px">
      <el-form-item label="">
        <el-input
          :disabled="true"
          v-model="username"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-radio disabled v-model="role" label="admin"></el-radio>
        <el-radio disabled v-model="role" label="user"></el-radio>
      </el-form-item>
      <el-form-item label="">
        <el-input
          placeholder=""
          type="password"
          v-model="formData.prePassword"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input
          placeholder=""
          type="password"
          v-model="formData.newPassword">
        </el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input
          placeholder=""
          type="password"
          v-model="formData.confirmPassword">
        </el-input>
      </el-form-item>
      <el-button @click="submit" :type="buttonType"></el-button>
      <span @click="handleBack" class="back"></span>
    </el-form>
  </div>
</template>

<script>
import { BUTTON_TYPE } from 'common/base'
import { userModify, adminModify } from 'api/account'
export default {
  name: 'ModifyPassword',
  data () {
    return {
      username: '',
      role: '',
      buttonType: BUTTON_TYPE,
      formData: {
        prePassword: '',
        newPassword: '',
        confirmPassword: ''
      }
    }
  },
  methods: {
    submit () {
      const formData = this.formData
      if (formData.prePassword.length < 6 || formData.newPassword.length < 6) {
        this.$errorNotify('6')
        return false
      } else if (formData.newPassword !== formData.confirmPassword) {
        this.$errorNotify('')
        return false
      }
      let promise
      let _returnPath
      if (this.role === 'admin') {
        promise = adminModify(formData.prePassword, formData.newPassword)
        _returnPath = {path: '/admin/index'}
      } else {
        promise = userModify(formData.prePassword, formData.newPassword)
        _returnPath = {path: '/user/resource'}
      }
      promise
        .then(res => {
          if (res.code === 2000) {
            this.$successToast('')
            setTimeout(() => {
              this.$router.push(_returnPath)
            }, 3000)
          } else {
            this.$errorNotify(res.message)
          }
        })
    },
    handleBack () {
      this.$router.go(-1)
    }
  },
  activated () {
    this.username = sessionStorage.username
    this.role = sessionStorage.role
  }
}
</script>

<style lang="stylus" scoped>
.box
  position fixed
  top 50%
  left 50%
  transform translate(-50%,-75%)
  background #fff
  padding 30px
  border-radius 5px
  width 400px
  text-align center
  .toggle
    margin-left 10px
    color #ffa100
  .back
    margin-left 20px
    color #ffa100
    cursor pointer
    font-size 14px
    text-decoration underline
</style>



